import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { FunctionConfigComponent } from './function-config/function-config.component';//功能配置
import { ConfigurationComponent } from './configuration/configuration.component';
const routes: Routes = [
    {
        path: 'function-config', component: FunctionConfigComponent,

    },
    {
        path: 'configuration', component: ConfigurationComponent,

    },
];

export const ProductFunctionRouting: ModuleWithProviders = RouterModule.forChild(routes);
