import { Component, OnInit } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { StepsModule, MenuItem, Message, ConfirmationService, SelectItem } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ProductManageHttpService } from "./../../http/product-manage.http.service";
import { BpcBean, ComponentViewBean, ProductFunctionBean } from './bean/configuration.bean';
import * as _ from 'lodash';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config'//中文

@Component({
  selector: 'app-configuration',
  templateUrl: './configuration.component.html',
  styleUrls: ['./configuration.component.scss'],
  providers: [ProductManageHttpService, Commfunc, ConfirmationService]
})
/**
 * 功能配置
 */
export class ConfigurationComponent implements OnInit {
  surveyApplTypeLists: any;
  productContentList: any = [];
  innerProductId: any;
  productName: any;
  productId: any;
  code: any = [];
  isMybankCust: any = [];
  QuestionLists: any = [];
  surveyApplTypeList: any = [];
  updateValue: any = [];
  showModel: string;
  headerTitle: string;
  display: boolean;
  contentName: any;
  payments: number;
  cars_paytool: any = [];
  productContentTypeId: any;
  resultListAllse: any = [
    { 'label': '请选择', 'value': '' },
  ];
  paymentMethod: any = [];
  productFunctionName: any;
  productFunctionId: any;
  //总页数
  total: string;
  //每页多少条
  items: MenuItem[];//下一步
  vlaue1: any = 0;//div
  activeIndex: number = 0;
  //分页
  pageSize: any = 10;
  fromDate: Date = new Date();//引入日期
  thruDate: Date = new Date();//引入日期
  pageNum: any = 1;
  ch: any = CALENDAR_CN;//讲英文转化为中文
  msgs: any = [];
  resultListAlls: any = [];
  paymentMethodList: any = [];
  aIndex: number = 0;
  isMybank: any = [];
  constructor(
    public productManageHttpService: ProductManageHttpService,
    public router: Router,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    private routerA: ActivatedRoute,
  ) {
    this.codeValues();
    this.isMybank = this.code['isMybankCust'];
    this.isMybankCust = this.isMybank.splice(1);
  }

  ngOnInit() {
    this.routerA.params.subscribe((data) => {
      this.productFunctionId = data.productFunctionId;
      this.productFunctionName = data.productFunctionName;
      this.productId = data.productId;
      this.productName = data.productName;
      this.innerProductId = data.insideProductId;
    });
    this.items = [
      {
        label: '支付',
        command: (event: any) => {
          this.activeIndex = 0;
        }
      },
      {
        label: '交付',
        command: (event: any) => {
          this.activeIndex = 1;
        }
      },
      {
        label: '个性化',
        command: (event: any) => {
          this.activeIndex = 2;
        }
      },
    ];
    this.prodFuncQueryPayTools();
    this.queryQuestionType();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //支付工具查询
  prodFuncQueryPayTools() {
    let param = {
      pageNum: 1,
      pageSize: 10000
    };
    this.productManageHttpService.prodFuncQueryPayTools(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.paymentMethodList = data.paymentMethodList;
        this.paymentMethodList.forEach(item => {
          item['show'] = 'N';
        });
        setTimeout(() => {
          this.queryProductContent();
        }, 200);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //支付工具选择
  paymentMethodClick(i, col) {
    let result = this.resultListAlls.indexOf(col.paymentMethodTypeId);
    if (result == '-1') {
      this.resultListAlls.push(col.paymentMethodTypeId);
      this.paymentMethodList[i].show = 'Y';
    } else {
      this.resultListAlls.splice(result);
      this.paymentMethodList[i].show = 'N';
    }
    this.paymentMethodList = _.clone(this.paymentMethodList);
  }
  queryProductContent() {
    let parem = {
      innerProductId: this.innerProductId,
      surveyApplTypeId: 'CART_ADD',
    };
    this.productManageHttpService.queryProductContent(parem).subscribe(data => {
      if (data.returnCode.code == "success") {
        if (data.productContentList.length > 0) {
          this.productContentList = data.productContentList;
          let paymentMethodTypeList = this.productContentList[0].paymentMethodTypeList;
          this.cars_paytool = this.productContentList[0].contentList;
          this.cars_paytool.forEach((item, i) => {
            let fromDate = null;
            let thruDate = null;
            if (!item.thruDate) {
              this.cars_paytool[i].thruDate = '';
            } else {
              thruDate = new Date(item.thruDate);
              this.cars_paytool[i].thruDate = this.commfunc.transDateN(thruDate);
            }
            if (!item.fromDate) {
              this.cars_paytool[i].fromDate = '';
            } else {
              fromDate = new Date(item.fromDate);
              this.cars_paytool[i].fromDate = this.commfunc.transDateN(fromDate);
            }
          });
          this.surveyApplTypeLists = this.productContentList[0].surveyOptionList[0].questionList;
          if (paymentMethodTypeList.length > 0) {
            this.paymentMethodList.forEach(items => {
              paymentMethodTypeList.forEach(item => {
                if (items.paymentMethodTypeId == item.paymentMethodTypeId) {
                  items['show'] = 'Y';
                  this.resultListAlls.push(items.paymentMethodTypeId);
                }
              });
            });
          }
        }
        // if (this.cars_paytool && this.cars_paytool.length > 0) {
        //   this.cars_paytool.forEach((item, i) => {
        //     let fromDate = null;
        //     let thruDate = null;
        //     if (!item.thruDate) {
        //       this.cars_paytool[i].thruDate = '';
        //     } else {
        //       thruDate = new Date(item.thruDate);
        //       this.cars_paytool[i].thruDate = this.commfunc.transDateN(thruDate);
        //     }
        //     if (!item.fromDate) {
        //       this.cars_paytool[i].fromDate = '';
        //     } else {
        //       fromDate = new Date(item.fromDate);
        //       this.cars_paytool[i].fromDate = this.commfunc.transDateN(fromDate);
        //     }
        //   });
        //   this.cars_paytool = _.clone(this.cars_paytool);
        // }
        if (this.surveyApplTypeLists && this.surveyApplTypeLists.length > 0) {
          this.surveyApplTypeLists.forEach((item, i) => {
            this.surveyApplTypeList.push({
              surveyOptionType: item.surveyOptionType,//暂时没有返值
              surveyOptionName: item.surveyOptionName,
              surveyOptionId: item.surveyOptionId,
              surveyOptionDesc: item.surveyOptionDesc,//暂时没有返值
              requiredField: item.requiredField,
              pageNum: item.pageNum,
            });
          });
        }
        this.paymentMethodList = _.clone(this.paymentMethodList);
        this.cars_paytool = _.clone(this.cars_paytool);
        this.surveyApplTypeList = _.clone(this.surveyApplTypeList);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  };
  //返回
  goBack() {
    window.history.go(-1)
  }
  next() {
    let kk = document.getElementById('KK');
    let contents = kk.getElementsByClassName('ui-steps-number');
    for (let i = 0; i < contents.length; i++) {
      contents[i]['style'].backgroundColor = '#757575';
      if (i <= this.activeIndex) {
        contents[i]['style'].backgroundColor = '#71d071';
      }
      if (i == (this.activeIndex + 1)) {
        contents[i]['style'].backgroundColor = '#ffc107';
      }
    }
    if (this.activeIndex == 0) {
      this.paymentMethod = [];
      this.paymentMethodList.forEach(e => {
        if (e.show == 'Y') {
          this.paymentMethod.push(e);
        }
      });
      this.activeIndex = 1;
      this.vlaue1 = 1;
      this.queryContentType();
      // if (this.paymentMethod.length > 0) {
      //   this.activeIndex = 1;
      //   this.vlaue1 = 1;
      //   this.queryContentType();
      // } else {
      //   this.msgs = [];
      //   this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择工具！' });
      // }
    } else if (this.activeIndex == 1) {
      this.activeIndex = 2;
      this.vlaue1 = 2;
    }
  }
  //属性值类型
  queryQuestionType() {
    this.productManageHttpService.queryQuestionType({}).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.QuestionLists = [];
        let obj = data.questionTypeMap;
        for (var key in obj) {
          this.QuestionLists.push({
            label: obj[key],
            value: key,
          });
        }
        this.QuestionLists = _.clone(this.QuestionLists);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  prev() {
    let kk = document.getElementById('KK');
    let contents = kk.getElementsByClassName('ui-steps-number');
    for (let i = 0; i < contents.length; i++) {
      contents[i]['style'].backgroundColor = '#757575';
      if (i < this.activeIndex - 1) {
        contents[i]['style'].backgroundColor = '#71d071';
      }
      if (i == (this.activeIndex - 1)) {
        contents[i]['style'].backgroundColor = '#ffc107';
      }
    }
    if (this.activeIndex == 2) {
      this.activeIndex = 1;
      this.vlaue1 = 1;
    } else if (this.activeIndex == 1) {
      this.activeIndex = 0;
      this.vlaue1 = 0;
    }
  }
  refresh() {
    this.thruDate = null;
  }
  //交付服务类型下拉列表查询
  queryContentType() {
    this.productManageHttpService.queryContentType({}).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.resultListAllse = [
          { 'label': '请选择', 'value': '' },
        ];
        let obj = data.contentTypeMap;
        for (var key in obj) {
          this.resultListAllse.push({
            label: obj[key],
            value: key,
          });
        }
        this.resultListAllse = _.clone(this.resultListAllse);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  severClick() {
    let check = [{
      surveyTypeId: 'CART_ADD',
      surveyTypeName: '购物车',
      questonList: this.surveyApplTypeList
    }];
    let param = {
      contentList: this.cars_paytool,
      paymentMethodTypeList: this.paymentMethod,
      surveyOptionList: check,
      functionName: this.productFunctionName,
      functionId: this.productFunctionId,
      productId: this.productId,
      productName: this.productName,
      insideProductId: this.innerProductId
    };
    this.productManageHttpService.updateBaseProductFunction(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '保存成功!' });
        this.goBack();
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  addpaytool() {
    this.payments = -1;
    // if (this.productContentTypeId && this.contentName && this.fromDate && this.thruDate) {
    if (this.productContentTypeId && this.contentName && this.fromDate) {
      let paytool = 0;
      if (this.cars_paytool.length > 0) {
        this.cars_paytool.forEach(item => {
          if (item.contentName == this.contentName) {
            paytool++;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "该服务名称已存在!" });
          }
        });
      }
      if (paytool == 0) {
        this.cars_paytool.push({
          productContentTypeId: this.productContentTypeId,
          contentName: this.contentName,
          fromDate: this.commfunc.transDateN(this.fromDate),
          thruDate: this.commfunc.transDateN(this.thruDate)
        });
        this.productContentTypeId = '';
        this.contentName = '';
        this.thruDate = null;
        this.fromDate = null;
      }
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "信息填写完整!" });
    }
    this.cars_paytool = _.clone(this.cars_paytool);
  }
  handRowSelect(event) {
    this.productContentTypeId = event.data.productContentTypeId;
    this.contentName = event.data.contentName;
    this.fromDate = new Date(event.data.fromDate);
    if (event.data.thruDate == '') {
      this.thruDate = null;
    } else {
      this.thruDate = new Date(event.data.thruDate);
    }
    this.cars_paytool.forEach((i, item) => {
      if (i.contentName == this.contentName) {
        this.payments = item;
      }
    });
  }
  //修改交付服务
  modifypaytool() {
    // if (this.productContentTypeId && this.contentName && this.fromDate && this.thruDate) {
    if (this.productContentTypeId && this.contentName && this.fromDate) {
      let paytool = 0;
      this.cars_paytool.forEach(item => {
        if (item.contentName == this.contentName) {
          paytool++;
        }
      });
      if (paytool == 0 || paytool == 1) {
        if (!this.cars_paytool[this.payments]) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "列表中没有此条信息!" });
          return
        }
        this.cars_paytool[this.payments].productContentTypeId = this.productContentTypeId;
        this.cars_paytool[this.payments].contentName = this.contentName;
        this.cars_paytool[this.payments].fromDate = this.commfunc.transDateN(this.fromDate);
        this.cars_paytool[this.payments].thruDate = this.commfunc.transDateN(this.thruDate);
        this.payments = -1;
        this.productContentTypeId = '';
        this.contentName = '';
        this.thruDate = null;
        this.fromDate = null;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "该服务名称已存在!" });
      }
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "信息填写完整!" });
    }
    this.cars_paytool = _.clone(this.cars_paytool);
  }
  //删除交付服务
  delpaytool() {
    let payment = 0;
    this.cars_paytool.forEach(item => {
      if (item.contentName == this.contentName) {
        payment++;
      }
    });
    if (payment > 0) {
      this.cars_paytool.splice(this.payments, 1);
      this.productContentTypeId = '';
      this.contentName = '';
      this.thruDate = null;
      this.fromDate = null;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "列表中没有此条信息!" });
    }
    this.cars_paytool = _.clone(this.cars_paytool);
    this.payments = -1;
  }
  //个性化（调查项）新增
  addintelmation() {
    this.display = true;
    this.headerTitle = '个性化新增';
    this.showModel = '2';
    this.updateValue = this.surveyApplTypeList;
  }
  //个性化新增的回调
  addintelmationCall(param) {
    this.display = param.display;
    if (param.list) {
      this.surveyApplTypeList = param.list;
    }
    this.surveyApplTypeList = _.clone(this.surveyApplTypeList);
  }
  //删除个性化
  deleteClick(item) {
    let checkse = -1;
    this.surveyApplTypeList.forEach((items, m) => {
      if (item.surveyOptionName == items.surveyOptionName) {
        checkse = m;
      }
    });
    this.surveyApplTypeList.splice(checkse, 1);
    this.surveyApplTypeList = _.clone(this.surveyApplTypeList);
  }
}