import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { FunctionConfigBean } from './../bean/function-config.bean';
import { SelectItem } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from '../../../http/product-manage.http.service';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';

@Component({
  selector: 'app-function-config-add',
  templateUrl: './function-config-add.component.html',
  styleUrls: ['./function-config-add.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
/**
 */
export class FunctionConfigAddComponent implements OnInit, OnChanges {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  select_1: any = []; //
  select_value: any; //
  userform: FormGroup; // 校验
  iscompele: boolean = false;
  fcBean: FunctionConfigBean = new FunctionConfigBean();

  // 提示信息
  msgs: Message[] = [];
  functionTypeV: any;
  productFunctionIdhead: any = 'FUNC';
  productFunctionId: any;
  code: any;
  constructor(
    public productManageHttpService: ProductManageHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc,
  ) {
    this.userform = fb.group({
      'productFunctionName': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
      'productFunctionId': new FormControl('', [Validators.pattern(/^[0-9]\d*$/), Validators.required]),
      productFunctionDes: [''],
    });
  }

  ngOnInit() {
    this.getSeqNo();
  }
  ngOnChanges() {
  }
  // 获取默认编码
  getSeqNo() {
    let parms = {
      seqType: 'PRODUCT_FUNCTION',
    };
    this.productManageHttpService.getSeqNo(parms).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.fcBean.productFunctionId = data.seqNo;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  // 添加
  addSaveBtn() {
    this.iscompele = true;
    // 验证
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    // 处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        this.iscompele = false;
        return;
      }
    }
    let productFunctionId = this.fcBean.productFunctionId.split('');
    if (productFunctionId.length == 6) {
      // 提交服务
      let parsm = {
        productFunctionId: this.productFunctionIdhead + this.fcBean.productFunctionId,
        productFunctionName: this.fcBean.productFunctionName,
        productFunctionDes: this.fcBean.productFunctionDes
      };
      this.productManageHttpService.productFunctionAdd(parsm).subscribe(data => {
        if (data.returnCode.code == "success") {
          this.outValue.emit(false);
          this.fcBean = new FunctionConfigBean();
          let parm = {
            display: false,
            code: data.errorMessage
          };
          this.outValue.emit(parm);
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
          this.iscompele = false;
        } else {
          this.iscompele = false;
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        }
      }, error => {
        this.iscompele = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      });
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "功能编号必须是FUNC+6位数字!" });
    }
  }
  // 取消
  addCloseBtn() {
    let parm = {
      display: false,
      code: 'cancelModel'
    };
    this.outValue.emit(parm);
  }
}
