import { Component, OnInit, OnChanges } from '@angular/core';
import { SelectItem } from 'primeng/primeng';
import { FunctionConfigBean } from './bean/function-config.bean';
import { ConfirmationService } from 'primeng/primeng';//删除
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ProductManageHttpService } from '../../http/product-manage.http.service';

@Component({
    selector: 'app-function-config',
    templateUrl: './function-config.component.html',
    styleUrls: ['./function-config.component.scss'],
    providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * 产品功能
 */
export class FunctionConfigComponent implements OnInit {
    //分页控制
    first: number = 0;
    firsts: number = 0;
    tabMesg: any = tableMessage;//列表无数据
    // bean
    fcBean: FunctionConfigBean = new FunctionConfigBean();
    //修改的参数
    updateValue: any = [];
    detailList: any = [];
    //列表
    cars_2: any;
    arr_2: any;
    //提示信息
    msgs: Message[] = [];
    //总页数
    total: string;
    totals: string;
    //每页多少条
    pageSize: number = 10;
    pageNum: number = 1;
    //模态框
    showModel: string;
    display: boolean = false;
    uration: boolean = false;
    displayA: boolean = false;
    //模态框的标题
    headerTitle: string;

    // 产品类型
    functionTypeV: any;
    col: any;
    code: any;
    functionTypeValue: string;
    constructor(
        public productManageHttpService: ProductManageHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.functionTypeV = this.code['functionTypes'];
    }
    ngOnInit() {
        this.first = 0;
        this.queryClick();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    // 重置
    resetClick() {
        this.fcBean = new FunctionConfigBean();
    }

    //查询始终第一页
    querySecle() {
        this.fcBean.pageSize = 10;
        this.fcBean.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }

    //  查询
    queryClick() {
        this.productManageHttpService.productFunctionQueryList(this.fcBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.cars_2 = data.functionList;
                this.total = data.size;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //删除
    deleteClick(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                //条件
                let param = { productFunctionId: item.productFunctionId };
                //提交服务
                this.productManageHttpService.productFunctionDelete(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.first = 0;
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        })
    }

    //新增
    addClick() {
        this.display = true;
        this.headerTitle = '新增';
        this.showModel = '1';
    }
    queryFunbase() {
            let param = {
                productFunctionId: this.col.productFunctionId,
                productFunctionName: this.col.productFunctionName,
                pageNum: this.pageNum,
                pageSize: this.pageSize
            }
        this.productManageHttpService.queryPFList(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.totals = data.total;
                this.detailList = data.detailList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }
    configueClick(item) {
        this.uration = true;
        this.headerTitle = '功能配置';
        this.col = item;
        this.queryFunbase();
    }
    paginateFun(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        //调用查询的方法
        this.queryFunbase();
        this.pageNum = 1;
    }
    //新增的回调
    addCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
            this.display = param.display;
        }
        this.first = 0;
        this.queryClick();
    }

    // 修改按钮
    modifyClick(item) {
        this.display = true;
        this.headerTitle = '修改';
        this.showModel = '2';
        this.updateValue = item;
    }

    //修改的回调
    updateCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
            this.display = param.display;
        }
        this.first = 0;
        this.queryClick();
    }

    basicDetails(item) {
        this.displayA = true;
        this.headerTitle = '详情';
        this.showModel = '3';
        this.updateValue = item;
    }

    detailsCall(param) {
        this.displayA = param;
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.fcBean.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.fcBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.fcBean.pageSize;
        //调用查询的方法
        this.queryClick();
        this.fcBean.pageNum = 1;
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}
