import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { FunctionConfigBean } from './../bean/function-config.bean';
import { SelectItem } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ProductManageHttpService } from '../../../http/product-manage.http.service';

@Component({
  selector: 'app-function-config-details',
  templateUrl: './function-config-details.component.html',
  styleUrls: ['./function-config-details.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
export class FunctionConfigDetailsComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  msgs: Message[] = [];
  list: any;
  // 产品类型
  functionTypeV: any;
  code: any;

  constructor(
    public productManageHttpService: ProductManageHttpService,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.functionTypeV = this.code['functionTypes'];
  }

  ngOnInit() {
    this.list = this.inValue;
    this.productFunctionDetail();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //取消
  addCloseBtn() {
    this.outValue.emit(false);
  }
  productFunctionDetail() {
    let parem = {
      productFunctionId: this.inValue.productFunctionId
    };
    this.productManageHttpService.productFunctionDetail(parem).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.list.productLineCrtUser = data.createdByUserLogin;
        let createdStamp = new Date(data.createdStamp);
        this.list.productLineCrtTime = this.commfunc.transDateN(createdStamp);
        this.list.productLineModUser = data.lastModifiedByUserLogin;
        let lastUpdatedStamp = new Date(data.lastUpdatedStamp);
        this.list.productLineModTime = this.commfunc.transDateN(lastUpdatedStamp);
        // this.total = data.size;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
}
