import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { FunctionConfigBean } from './../bean/function-config.bean';
import { SelectItem } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from '../../../http/product-manage.http.service';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';

@Component({
  selector: 'app-function-config-modify',
  templateUrl: './function-config-modify.component.html',
  styleUrls: ['./function-config-modify.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
/**
 * // 产品功能修改
 */
export class FunctionConfigModifyComponent implements OnInit, OnChanges {
  userform: FormGroup; // 校验
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  select_1: any = []; // 默认下拉产品线编号
  select_value: any; // 产品线编号
  // bean
  fcBean: FunctionConfigBean = new FunctionConfigBean();
  // 提示信息
  msgs: Message[] = [];
  iscompele: boolean = false;
  // 产品类型
  functionTypeV: any;
  code: any;
  functionTypeValue: string = '';
  constructor(
    public productManageHttpService: ProductManageHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc,
  ) {
    this.userform = fb.group({
      'productFunctionName': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
      productFunctionId: ['', Validators.required],
      productFunctionDes: [''],
    });
    this.codeValues(); // 调用方法，获取全部码值
    this.functionTypeV = this.code['functionTypes'];
  }

  ngOnInit() {
    this.select_1 = [
      { label: '请选择', value: "" },
    ];
    this.select_1 = [
      { label: '请选择', value: "" },
    ];
    this.refresh();
  }

  ngOnChanges() {
    this.line_query();
  }
  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; // code是自己声明的变量
  }
  refresh() {
    this.fcBean = Object.assign({}, this.inValue);
  }
  line_query() {
    let param = { productFunctionId: this.inValue.productFunctionId };
    this.productManageHttpService.getProductFunction(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.fcBean = data;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  // 添加
  updataBtn() {
    this.iscompele = true;
    // 验证
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        this.iscompele = false;
        return;
      }
    }
    // 提交服务
    this.productManageHttpService.productFunctionUpdate(this.fcBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.fcBean = new FunctionConfigBean();
        let parm = {
          display: false,
          code: data.errorMessage
        };
        this.outValue.emit(parm);
        this.iscompele = false;
      } else {
        this.iscompele = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.iscompele = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  // 取消
  addCloseBtn() {
    let parm = {
      display: false,
      code: 'cancelModel'
    };
    this.outValue.emit(parm);
  }
}
